import Head from 'next/head'

class Meta extends React.Component {
  constructor(props) {
    super(props)

    if (!this.props.title) {
      this.props.title = 'Interactions Lab | University of Calgary HCI Group'
    }
    if (!this.props.description) {
      this.props.title = 'Human-Computer Interaction and Information Visualization Group at the University of Calgary'
    }
    if (!this.props.keywords) {
      this.props.keywords = 'Human-Computer Interaction, HCI, Information Visualization, University of Calgary, CHI, UIST'
    }
    if (!this.props.image) {
      this.props.image = '/static/images/cover.jpg'
    }
    if (!this.props.url) {
      this.props.url= 'https://ilab.ucalgary.ca/'
    }
  }


  render() {
    return (
      <Head>
        <title>{title}</title>
        <meta name="keywords" content={ this.props.keywords } />
        <meta name="description" content={ this.props.description } />

        <meta property="og:title" content={ this.props.title } />
        <meta property="og:description" content={ this.props.description } />
        <meta property="og:site_name" content="Interactions Lab | University of Calgary HCI Group" />
        <meta property="og:url" content={ this.props.url } />
        <meta property="og:image" content={ this.props.image } />
        <meta property="og:type" content="website" />

        <meta name="twitter:card" content="summary" />
        <meta name="twitter:site" content="https://ilab.ucalgary.ca/" />
        <meta name="twitter:image" content="/static/images/cover.jpg" />
      </Head>
    )
  }
}

export default Meta